import React from 'react';
import logo from './logo.svg';
import './App.css';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { Button, Slider, Stack } from '@mui/material';

const unikaPersonalities = [
  'foodies',
  'gamer',
  'lifestylist',
  'sportier',
  'spiritual',
  'adventure',
  'gardener',
  'artisti',
  'sole',
  'famil',
  'centric',
  'learner',
  'cooker',
  'spoile',
  'cat',
  'traveler',
  'anima',
  'lover',
  'diy',
  'hom',
  'decorator',
  'wellnes',
  'enthusiasti',
  'environmentalist',
  'chocolat',
  'lover',
  'music',
  'digger',
  'boo',
  'wor',
  'fashionista',
  'entertainmen',
  'rule',
  'sophisticate',
  'drink'
];

function App() {

  const marks = [
    {
      value: 0,
      label: '0₪',
    },
    {
      value: 1000,
      label: '1,000₪',
    },
  ];

  function valuetext(value: number) {
    return `${value}°C`;
  }

  function valueLabelFormat(value: number) {
    return marks.findIndex((mark) => mark.value === value) + 1;
  }

  return (
    <div className="App">
      <header className="App-header">
      </header>
      <div className='unika-wrapper flex-col'>
        <Stack className='unika-inner-wrapper'>
          <p>Choose personality</p>
          <Autocomplete
            disablePortal
            id="combo-box-demo"
            options={unikaPersonalities.map((personality, index) => ({ 'label': personality, 'id': index }))}
            sx={{ width: 300 }}
            renderInput={(params) => <TextField {...params} label="Personality" />}
          />
        </Stack>

        <Stack className='unika-inner-wrapper'>
          <p>Set minimum and maximum price</p>
          <Slider
            defaultValue={50}
            aria-label="Default"
            valueLabelDisplay="auto"
            sx={{ width: 300 }}
            marks={marks}
            min={0}
            max={1000} />
        </Stack>
        <Button variant="contained">search</Button>

      </div>
    </div>
  );
}

export default App;
